
import { Options, Vue } from 'vue-class-component';
import { Item } from '@/components';

@Options({
  components: { Item },
})
export default class Home extends Vue {
  term = '';

  get left(): Item[] {
    return this.term
      ? this.$store.getters.search(this.term)
      : this.$store.state.left;
  }
}
